import Vue from 'vue'
import VueRouter from 'vue-router'
import Upload from '../views/Upload.vue'
import List from '../views/List.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'upload',
    component: Upload
  },
  {
    path: '/uploads',
    name: 'list',
    component: List
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
