<template>
  <div class="list">

    <ImageInfo
      v-for="(image, image_index) in images"
      v-bind:key="image._id"
      v-bind:image="image"
      v-on:image_deleted="remove_image(image_index)"/>


    <!--<button type="button" name="button" v-on:click="drop()">DROP</button>-->

  </div>
</template>

<script>
// @ is an alias to /src
import ImageInfo from '@/components/ImageInfo.vue';

export default {
  name: 'Home',
  data(){
    return {
      images: []
    }
  },
  components: {
    ImageInfo,
  },
  mounted(){
    this.get_images()
  },
  methods: {
    get_images(){
      this.axios.get(`${process.env.VUE_APP_API_URL}/list`)
      .then(response => {
        this.images.splice(0,this.images.length)
        response.data.forEach((image) => {
          this.images.push(image)
        });
      })
      .catch(error => console.log(error))
    },
    remove_image(image_index){
      this.images.splice(image_index,1)
    },

    drop(){
      if(confirm('Really?')){
        this.axios.post(`${process.env.VUE_APP_API_URL}/drop`)
        .then( () => {
          this.get_images()
        })
        .catch(error => console.log(error))
      }

    }
  },
}
</script>

