<template>
  <div class="home">
    <form
      class=""
      v-on:submit.prevent="submit()"
      v-if="!url">
      <input type="file" ref="image_input" name="image">
      <input type="submit" name="">
    </form>
    <div class="" v-else>
      <input type="text" ref="image_url" readonly v-bind:value="url">
      <content-copy-icon
        v-on:click="copy_url()"/>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
//import HelloWorld from '@/components/HelloWorld.vue'
import ContentCopyIcon from 'vue-material-design-icons/ContentCopy.vue';

export default {
  name: 'Home',
  components: {
    ContentCopyIcon
  },
  data(){
    return {
      url: null
    }
  },
  methods: {
    submit(){

      let formData = new FormData();
      formData.append('image', this.$refs.image_input.files[0]);
      this.axios.post(`${process.env.VUE_APP_API_URL}/upload`, formData, {
        headers: {'Content-Type': 'multipart/form-data' }
      })
      .then(response => {
        this.url = `${process.env.VUE_APP_API_URL}/image?id=${response.data._id}`
      })
      .catch(error => console.log(error));

    },

    copy_url() {
      let url_input = this.$refs.image_url
      /* Select the text field */
      url_input.select();
      url_input.setSelectionRange(0, 99999); /*For mobile devices*/

      /* Copy the text inside the text field */
      document.execCommand("copy");

    }
  }
}
</script>

<style soped>
form {
  text-align: center;
}
</style>
