<template>
  <div class="image">
    <img v-bind:src="image_url">

    <div class="">

      <div class="">
        ID: {{image._id}}
      </div>
      <div class="">
        Size: {{image.size/1000}}kB
      </div>
      <div class="">
        Upload date: {{image.upload_date}}
      </div>
      <div class="">
        URL:
        <input
          type="text"
          readonly
          ref="image_url"
          v-bind:value="image_url">
        <content-copy-icon
          v-on:click="copy_url()"/>
      </div>
      <div class="">
        <delete-icon v-on:click="delete_image()"/>
      </div>
    </div>
  </div>
</template>

<script>
import ContentCopyIcon from 'vue-material-design-icons/ContentCopy.vue';
import DeleteIcon from 'vue-material-design-icons/Delete.vue';


export default {
  name: 'ImageInfo',
  props: {
    image: Object
  },
  components: {
    ContentCopyIcon,
    DeleteIcon,
  },
  methods: {
    copy_url() {
      let url_input = this.$refs.image_url
      /* Select the text field */
      url_input.select();
      url_input.setSelectionRange(0, 99999); /*For mobile devices*/

      /* Copy the text inside the text field */
      document.execCommand("copy");

    },
    delete_image(){
      if(confirm('Really?')){
        this.axios.post(`${process.env.VUE_APP_API_URL}/delete`, {
          id: this.image._id
        })
        .then( () => { this.$emit('image_deleted') })
        .catch(error => console.log(error))
      }
    }
  },
  computed: {
    image_url(){
      return process.env.VUE_APP_API_URL + '/image?id=' + this.image._id;
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.image {
  border: 1px solid #dddddd;
  margin: 5px 0;
  padding: 5px;
  display: flex;
}

.image img{
  width: 100px;
  height: 100px;
  object-fit: contain;
  margin-right: 5px;
}
</style>
